.Leaderboard {
    position: relative;
    width: 100%;
    margin: 5em 1em 2em 1em;
}
 .Leaderboard thead {
     display: none;
}
 .Leaderboard .pagination {
     display: none;
}
 .Leaderboard .react-bs-table-pagination {
     position: absolute;
     top: -18px;
     left: 0;
}
 .Leaderboard__title {
     font-size: 20px;
     text-align: center;
     text-transform: capitalize;
}
 .Leaderboard .table-bordered {
     border: 0px solid #EBEBEB !important;
}
 .Leaderboard .table-bordered > thead > tr > th,
 .Leaderboard .table-bordered > tbody > tr > th,
 .Leaderboard .table-bordered > tfoot > tr > th,
 .Leaderboard .table-bordered > thead > tr > td,
 .Leaderboard .table-bordered > tbody > tr > td,
 .Leaderboard .table-bordered > tfoot > tr > td {
     border: 0;
}
 .Leaderboard table tbody tr td:first-child {
     text-align: center !important;
     background: #c1c1c1;
     color: #fff;
     font-weight: bold;
}
.Leaderboard table tbody tr td:nth-child(2) {
    font-size: 16px;
}

 .Leaderboard table tbody tr td:nth-child(3) {
     text-align: center !important;
     background: #c1c1c1;
     color: #fff;
     font-weight: bold;
     font-size: 16px;
}
 .Leaderboard .btn {
     border: none;
     border-radius: 0;
     -webkit-transition: 0.3s;
     -o-transition: 0.3s;
     transition: 0.3s;
}
 .Leaderboard .btn:hover {
     border: none;
     border-radius: 0;
     -webkit-transition: 0.3s;
     -o-transition: 0.3s;
     transition: 0.3s;
}
 .Leaderboard .btn-default:hover, .Leaderboard .btn-default:focus, .Leaderboard .btn-default:active, .Leaderboard .btn-default.active, .Leaderboard .open .dropdown-toggle.btn-default, .Leaderboard .btn-default:active:focus, .Leaderboard .btn-default:active:hover, .Leaderboard .btn-default.active:hover, .Leaderboard .btn-default.active:focus {
     border: 0;
}
 .Leaderboard .dropdown-menu {
     border-radius:0 ;
     -webkit-transition: 0.3s;
     -o-transition: 0.3s;
     transition: 0.3s;
}
 .Leaderboard .dropdown-menu > li > a {
     border-radius: 0;
     -webkit-transition: 0.3s;
     -o-transition: 0.3s;
     transition: 0.3s;
}

.Leaderboard .table-bordered>tbody>tr>td {
    border: 1px solid #fff ;
}



